.ng-select.sl-select-search {
  height: 50px;

  .ng-select-container {
    height: 100% !important;
    border: none;
    background-color: #EFF5F7;
  }

  &.ng-select-opened {
    .ng-select-container {
      background-color: #EFF5F7;
    }
  }

  .ng-dropdown-panel {
    border-color: #e8eaeb;
  }

  .ng-value-container {
    padding-left: 30px !important;

    .ng-value {
      font-weight: 600;
      color: #000;
    }
  }
}

.ng-select.sl-select-feedback {
  height: 45px;
  font-size: 24px;

  &.error {
    .ng-select-container {
      border-color: #dd1e25;
    }
  }

  .ng-arrow-wrapper {
    margin-right: 15px;

    .ng-arrow {
      border: none;
      width: 25px;
      height: 20px;
      background-image: url(#{$IMG_PATH}/icons/icon-list.svg);
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
    }
  }

  .ng-select-container {
    height: 100% !important;
    color: #000;
    border: 1px solid #BCBCBC;
    border-radius: 0;
    cursor: pointer;

    .ng-value-container {
      padding-left: 20px;

      .ng-placeholder {
        color: #898888;
      }
    }
  }

  .ng-dropdown-panel {
    border-radius: 0;

    .ng-dropdown-panel-items {
      padding: 4px;
      box-sizing: border-box;

      .ng-option {
        padding: 10px 15px;
        box-sizing: border-box;
        color: #050505;

        &:hover {
          background-color: #F7F7F7;
        }
      }
    }
  }
}

.ng-select.search-area {

  .ng-select-container {
    height: 100% !important;
    border: 1px solid #E7EBEF;
    border-radius: 0;

    .ng-input {
      top: 50% !important;
      transform: translateY(-50%);
      input {
        padding: 0;
      }
    }
  }
}
