// this shame
$avatar-offset: 70;
$base-color: #F4F4F4;
$shine-color: rgba(229, 229, 229, 0.8);
$animation-duration: 2.0s;
@keyframes shine-avatar {
  0% {
    background-position: -100px + $avatar-offset;
  }
  40%,
  100% {
    background-position: 140px + $avatar-offset;
  }
}

@mixin background-gradient() {
  background-image: linear-gradient(90deg, $base-color 0px, $shine-color 40px, $base-color 80px);
  background-size: 600px;
}

@mixin theme($themes) {
  @each $theme,
  $map in $themes {
    .theme-#{$theme} & {
      $theme-map: () !global;
      @each $key,
      $submap in $map {
        $value: map-get(map-get($themes, $theme), '#{$key}');
        $theme-map: map-merge($theme-map, ($key: $value)) !global;
      }
      @content;
      $theme-map: null !global;
    }
  }
}

@mixin clearfix {
  &:after {
    content: '';
    display: table;
    clear: both;
  }
}

@mixin reset-input {
  padding: 0;
  margin: 0;
  border: 0;
  min-width: 0;
  background-color: transparent;
  cursor: pointer;
  &:focus {
    outline: none;
  }
}

@mixin verticalAlign {
  >.vertical-align {
    display: table;
    table-layout: fixed;
    width: 100%;
    height: 100%;
    border-collapse: collapse;
    .box {
      display: table-cell;
      vertical-align: middle;
    }
  }
}

@mixin aspect-ratio($width, $height) {
  position: relative;
  &:before {
    display: block;
    content: " ";
    width: 100%;
    padding-bottom: ($height / $width) * 100%;
  }
  >.content {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }
}

@mixin textEllipsis($lines, $lh, $auto: true) {
  display: -webkit-box;
  display: block;
  text-overflow: ellipsis;
  -webkit-line-clamp: $lines;
  -webkit-box-orient: vertical;
  overflow: hidden;
  @if ($auto==true) {
    max-height: $lines * $lh;
  }
  @if ($auto==false) {
    height: $lines * $lh;
  }
}

@function add($key) {
  @return map-get($theme-map, $key);
}

@function strip-unit($number) {
  @if type-of($number)=='number' and not unitless($number) {
    @return $number / ($number * 0 + 1);
  }
  @return $number;
}

@function calcFluidFontSize($f-min, $f-max, $w-min, $w-max, $units: px) {
  $f-min: strip-unit($f-min);
  $f-max: strip-unit($f-max);
  $w-min: strip-unit($w-min);
  $w-max: strip-unit($w-max);
  $k: ($f-max - $f-min)/($w-max - $w-min);
  $b: $f-min - $k * $w-min;
  $b: $b + $units;
  @return calc( #{$k} * 100vw + #{$b});
}

@mixin fluidFontSize($f-min, $f-max, $w-min, $w-max, $step-lh: false, $lines: false, $height: false, $fallback: false) {
  font-size: $f-min;
  @if($lines) {
    overflow: hidden;
    text-overflow: ellipsis;
  }
  @if ($step-lh) {
    line-height: $f-min + $step-lh;
  }
  @if ($lines and $height==false) {
    max-height: ($f-min + $step-lh) * $lines;
  }
  @if ($lines and $height) {
    height: ($f-min + $step-lh) * $lines;
  }
  @media (min-width: $w-min) {
    @if ($fallback) {
      font-size: $fallback;
    }
    font-size: calcFluidFontSize($f-min, $f-max, $w-min, $w-max, px);
    @if ($step-lh) {
      line-height: calcFluidFontSize($f-min + $step-lh, $f-max + $step-lh, $w-min, $w-max, px);
    }
    @if ($lines and $height==false) {
      max-height: calcFluidFontSize(($f-min + $step-lh) * $lines, $f-max * $lines + $step-lh, $w-min, $w-max, px);
    }
    @if ($lines and $height) {
      height: calcFluidFontSize(($f-min + $step-lh) * $lines, $f-max * $lines + $step-lh, $w-min, $w-max, px);
    }
    @if ($lines) {
      -webkit-line-clamp: $lines;
      -webkit-box-orient: vertical;
    }
  }
  @media (min-width: $w-max) {
    font-size: $f-max;
    @if ($step-lh) {
      line-height: $f-max + $step-lh;
    }
    @if ($lines and $height==false) {
      max-height: ($f-max + $step-lh) * $lines;
    }
    @if ($lines and $height) {
      height: ($f-max + $step-lh) * $lines;
    }
  }
}
