.app,
app-header {
  input[type="button"],
  input[type="submit"] {
    @include reset-input();
    &:hover {
      background-color: transparent;
    }
  }
  input[type="button"].btn-default,
  input[type="submit"].btn-default,
  input[type="submit"],
  .btn-default {
    font-size: 18px;
    height: 50px;
    line-height: 50px;
    color: #fff;
    background-color: #dd1e25;
    border: none;
    border-radius: 5px;
    padding: 0 25px;
    box-sizing: border-box;
    display: inline-block;
    white-space: nowrap;
    text-decoration: none;
    &:disabled,
    &.disabled {
      background-color: #ff8a93;
    }
    &:hover {
      background-color: rgb(223, 23, 57);
    }
    &:visited {
      text-decoration: none;
      color: #fff;
    }
    &.is-transparent {
      background-color: transparent;
      border: 1px solid #fff;
      &.red {
        color: #dd1e25;
        border-color: #dd1e25;
        padding: 0 45px;
        &:visited {
          color: #dd1e25;
        }
      }
    }
  }
  input[type="button"].btn-download,
  .btn-download {
    display: block;
    height: 35px;
    line-height: 35px;
    color: #fff !important;
    padding-right: 50px;
    position: relative;
    text-decoration: none !important;
    background-color: transparent !important;
    &:hover i {
      background-color: rgb(218, 37, 52);
    }
    i {
      position: absolute;
      right: 0;
      top: 0;
      bottom: 0;
      display: block;
      background-repeat: no-repeat;
      background-position: center 0;
      background-image: url(#{$IMG_PATH}/icons/icon-download-white.svg);
      background-size: 90%;
      height: 35px;
      width: 35px;
      border-radius: 100%;
      transition: background .3s;
      background-color: #dd1e25;
    }
  }
  input[type="button"].btn-to-back,
  .btn-to-back {
    display: inline-block;
    height: 50px;
    line-height: 50px;
    font-size: 22px;
    color: #768692;
    padding-left: 75px;
    box-sizing: border-box;
    position: relative;
    text-decoration: none !important;
    &:visited {
      color: #768692;
    }
    &:hover i {
      background-color: #9fb1bf;
    }
    i {
      position: absolute;
      left: 0;
      top: 0;
      bottom: 0;
      display: block;
      height: 50px;
      width: 50px;
      background-repeat: no-repeat;
      background-position: center;
      background-size: 80%;
      transition: background .3s;
      background-color: #768692;
      background-image: url(#{$IMG_PATH}/icons/icon-big-arrow-left-white.svg);
    }
  }
  input[type="button"].btn-search,
  input[type="button"].btn-notification,
  input[type="button"].btn-location,
  input[type="button"].btn-hamburger,
  input[type="button"].btn-services,
  .btn-services,
  .btn-search,
  .btn-hamburger {
    background-color: transparent;
    padding: 0;
    margin: 0;
    width: 50px;
    height: 50px;
    font-size: 0;
    cursor: pointer;
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    box-sizing: border-box;
    &.small {
      width: 32px;
      height: 32px;
    }
  }
  input[type="button"].btn-close,
  .btn-close {
    cursor: pointer;
    display: inline-block;
    border-radius: 0px;
    border-width: 0px;
    background-image: url("#{$IMG_PATH}/icons/icon-close-black.svg");
    background-repeat: no-repeat;
    background-size: 22px 22px;
    background-color: transparent !important;
    width: 50px;
    height: 50px;
    background-position: center;
  }
  .btn-find {
    display: inline-block;
    background-color: #dd1e25 !important;
    border: 0;
    cursor: pointer;
    height: 60px;
    width: 60px;
    margin: 0;
    background-image: url(#{$IMG_PATH}/icons/icon-search-white.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: 32px 35px;
    vertical-align: bottom;
    border-radius: 3px;
    @media screen and (max-width: $mq_md) {
      height: 50px;
      width: 50px;
    }
  }
  .btn-close-gray {
    cursor: pointer;
    display: inline-block;
    border-radius: 0px;
    border-width: 0px;
    background-image: url('#{$IMG_PATH}/icons/icon-close.svg');
    background-repeat: no-repeat;
    background-size: 22px 22px;
    width: 50px;
    height: 50px;
    background-position: center;
    background-color: transparent;
  }
  .btn-close-gray-bold {
    cursor: pointer;
    display: inline-block;
    border-radius: 0px;
    border-width: 0px;
    background-image: url('#{$IMG_PATH}/icons/icon-close-gray-bold.svg');
    background-repeat: no-repeat;
    background-size: 20px 20px;
    width: 50px;
    height: 50px;
    background-position: center;
    background-color: transparent;
    &:hover {
      background-color: transparent !important;
    }
  }
}
