.m-page-nav {
  display: inline-block;
  .to-begin {
    display: inline-block;
    height: 48px;
    color: #dd1e25;
    font-size: 18px;
    text-transform: uppercase;
    margin-right: 90px;
    line-height: 48px;
    vertical-align: top;
    cursor: pointer;
  }
  .disabled {
    color: #DEDFE0;
  }
  input[type="button"] {
    display: inline-block;
    vertical-align: top;
    width: 100px;
    height: 50px;
    background-size: cover;
    background-color: transparent;
    background-position: center;
    border: 0;
    padding: 0;
    cursor: pointer;
  }
  .prev-page {
    background-image: url('#{$IMG_PATH}/icons/icon-pagination-arrow-left-red.svg');
    margin-right: 60px;
    &.disabled {
      background-image: url('#{$IMG_PATH}/icons/icon-pagination-arrow-left-gray.svg');
    }
  }
  .next-page {
    background-image: url('#{$IMG_PATH}/icons/icon-pagination-arrow-right-red.svg');
    &.disabled {
      background-image: url('#{$IMG_PATH}/icons/icon-pagination-arrow-right-gray.svg');
    }
  }
}


.tabs-phonebook {
  .right-sidebar-opened & {
    @media screen and (max-width: 1800px) {
      margin-left: 0 !important;
      padding: 0 !important;
      padding-top: 30px !important;
      display: block !important;
    }
  }
}
