.l-body {
  position: relative;
  min-width: 1100px;
  max-width: 2048px;
}

.system-content {
  padding: 20px;
  box-sizing: border-box;
}

.l-body__page-box {
  background-color: white;
  padding: 45px;
  box-sizing: border-box;
}

.l-body__content-area {
  display: table;
  width: 100%;
  table-layout: fixed;
  border-collapse: collapse;
  height: 100%;
}

.l-body__navigation,
.l-body__content {
  display: table-cell;
  vertical-align: top;
  box-sizing: border-box;
  height: 100%;
}

.l-body__content {
  background-color: #fff;
  &.not-fill {
    background-color: #F7F7F7;
  }
}

.l-body__content-box {
  padding: 35px;
  box-sizing: border-box;
}

.l-body__content-header {
  position: relative;
  padding: 40px 20px;
  padding-right: $BODY_SEARCH_WIDTH + 35px;
  padding-top: 0;
  &:after {
    content: "";
    display: table;
    clear: both;
  }
}

.l-body__search {
  position: absolute;
  bottom: 30px;
  right: 35px;
  width: $BODY_SEARCH_WIDTH;
}

.l-body__navigation {
  width: $LEFT_SIDEBAR_WIDTH;
  background-color: #768692;
  position: relative;
}
.l-body__left-menu {
  position: absolute;
  left: 0;
  right: 0;
}

.l-body__navigation-control {
  width: 100%;
  height: 100px;
  border-collapse: collapse;
  box-sizing: border-box;
  border-top: 1px solid #8197AA;
  border-bottom: 1px solid #8197AA;
  .control-button {
    background-position: center;
    width: 100px;
    box-sizing: border-box;
    .btn-arrow-right {
      background-image: url('#{$IMG_PATH}/icons/icon-menu-arrow.svg');
      background-size: 50px;
      width: 100px;
      height: 100px;
    }
  }
}

.l-page {
  background-color: #fff;
  margin-left: -35px;
  padding-left: 35px;
  box-sizing: border-box;
}

.l-page-inner {
  display: table;
  table-layout: fixed;
  border-collapse: collapse;
  width: 100%;
  margin-bottom: $MARGIN_HOME;
}

.l-page__menu {
  width: 350px;
  vertical-align: top;
  display: table-cell;
  box-sizing: border-box;
  &+.l-page__content {
    padding-left: 40px;
  }
}

.l-page__menu-item {
  text-decoration: none;
  background-color: #DEDFE0;
  color: #000;
  display: table;
  height: 60px;
  width: 100%;
  border-bottom: 1px solid white;
  box-sizing: border-box;
  border-collapse: collapse;
  font-size: 18px;
  &:last-child {
    border-bottom: 0;
  }
  &.is-current-item {
    color: #fff;
    background-color: #A2B2C8;
  }
  span {
    display: table-cell;
    vertical-align: middle;
    padding: 10px 25px;
    box-sizing: border-box;
  }
}

.l-page-header {
  position: relative;
  padding-left: 25px;
  padding-right: 35px;
  padding-bottom: 65px;
  max-width: 1065px;
  @include clearfix();

  .title {
    margin-top: 15px;
    font-size: 34px;
    color: #000;
    box-sizing: border-box;
  }
}

.l-page__content,
.l-page__right-sidebar {
  display: table-cell;
  vertical-align: top;
  box-sizing: border-box;
}

.l-page__content {
  width: auto;
  &+.l-page__right-sidebar {
    padding-left: $CONTENT_PADDING;
  }
}

.l-page__right-sidebar {
  width: $RIGHT_SIDEBAR_WIDTH;
}

.l-breadcrumbs {
  font-size: 0;
  margin: 0 -11px;
}
.l-breadcrumb {
  color: #4D4E4D;
  display: inline-block;
  vertical-align: middle;
  padding: 0 11px;
  font-size: 14px;
  box-sizing: border-box;
  position: relative;

  &:after {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: -4px;
    content: "";
    background-image: url('#{$IMG_PATH}/icons/icon-arrow-right-gray-flat.svg');
    background-repeat: no-repeat;
    background-position: center;
    height: 11px;
    width: 8px;
  }

  &:last-child:after {
    content: none;
  }

  a {
    color: #4D4E4D;
    text-decoration: none;
    display: block;
  }
}
